"use client";

import { useState } from "react";
import { useQuery, useMutation, useAction } from "convex/react";
import { api } from "../../../convex/_generated/api";
import { Id } from "../../../convex/_generated/dataModel";
import Navigation from "@/components/Navigation";

export default function SendersPage() {
    const senders = useQuery(api.senders.list);
    const createSender = useMutation(api.senders.create);
    const deleteSender = useMutation(api.senders.remove);
    const testSmtp = useAction(api.email.testSmtp);

    const [showModal, setShowModal] = useState(false);
    const [name, setName] = useState("");
    const [fromEmail, setFromEmail] = useState("");
    const [fromName, setFromName] = useState("");
    const [smtpHost, setSmtpHost] = useState("");
    const [smtpPort, setSmtpPort] = useState("465");
    const [smtpUser, setSmtpUser] = useState("");
    const [smtpPass, setSmtpPass] = useState("");
    const [useSSL, setUseSSL] = useState(true);
    const [isDefault, setIsDefault] = useState(false);
    const [isSubmitting, setIsSubmitting] = useState(false);
    const [testResult, setTestResult] = useState<{ success: boolean; message: string } | null>(null);
    const [isTesting, setIsTesting] = useState(false);

    const handleCreate = async () => {
        if (!name.trim() || !fromEmail.trim() || !smtpHost.trim()) return;
        setIsSubmitting(true);
        try {
            await createSender({
                name: name.trim(),
                fromEmail: fromEmail.trim(),
                fromName: fromName.trim() || name.trim(),
                smtpHost: smtpHost.trim(),
                smtpPort: parseInt(smtpPort) || 465,
                smtpUser: smtpUser.trim() || fromEmail.trim(),
                useSSL,
                isDefault,
            });
            setShowModal(false);
            resetForm();
        } catch (error) {
            console.error(error);
        } finally {
            setIsSubmitting(false);
        }
    };

    const resetForm = () => {
        setName("");
        setFromEmail("");
        setFromName("");
        setSmtpHost("");
        setSmtpPort("465");
        setSmtpUser("");
        setSmtpPass("");
        setUseSSL(true);
        setIsDefault(false);
    };

    const handleDelete = async (id: Id<"senders">) => {
        if (confirm("Delete this sender configuration?")) {
            await deleteSender({ id });
        }
    };

    const handleTestSmtp = async () => {
        setIsTesting(true);
        setTestResult(null);
        try {
            const result = await testSmtp({});
            setTestResult(result);
        } catch (error) {
            setTestResult({ success: false, message: error instanceof Error ? error.message : "Test failed" });
        } finally {
            setIsTesting(false);
        }
    };

    return (
        <Navigation>
            <div>
                {/* Header */}
                <div style={{ display: "flex", justifyContent: "space-between", alignItems: "flex-start", marginBottom: 32 }}>
                    <div>
                        <h1 className="page-title">Senders</h1>
                        <p className="page-subtitle">Configure SMTP settings for sending emails</p>
                    </div>
                    <div style={{ display: "flex", gap: 12 }}>
                        <button className="btn btn-secondary" onClick={handleTestSmtp} disabled={isTesting}>
                            {isTesting ? "Testing..." : "üîå Test SMTP"}
                        </button>
                        <button className="btn btn-primary" onClick={() => setShowModal(true)}>
                            <span>+</span> Add Sender
                        </button>
                    </div>
                </div>

                {/* SMTP Test Result */}
                {testResult && (
                    <div
                        className="card"
                        style={{
                            marginBottom: 24,
                            background: testResult.success ? "rgba(16, 185, 129, 0.1)" : "rgba(239, 68, 68, 0.1)",
                            borderColor: testResult.success ? "rgba(16, 185, 129, 0.3)" : "rgba(239, 68, 68, 0.3)",
                        }}
                    >
                        <div style={{ display: "flex", alignItems: "center", gap: 12 }}>
                            <span style={{ fontSize: 24 }}>{testResult.success ? "‚úÖ" : "‚ùå"}</span>
                            <div>
                                <div style={{ fontWeight: 600, color: testResult.success ? "var(--accent-success)" : "var(--accent-danger)" }}>
                                    {testResult.success ? "SMTP Connection Successful" : "SMTP Connection Failed"}
                                </div>
                                <div style={{ fontSize: 14, color: "var(--text-secondary)", marginTop: 4 }}>{testResult.message}</div>
                            </div>
                        </div>
                    </div>
                )}

                {/* Senders Grid */}
                {!senders ? (
                    <div className="loading">
                        <div className="spinner" />
                    </div>
                ) : senders.length === 0 ? (
                    <div className="card">
                        <div className="empty-state">
                            <div className="empty-icon">‚öôÔ∏è</div>
                            <h3 className="empty-title">No senders configured</h3>
                            <p className="empty-text">Add SMTP configuration to start sending emails</p>
                            <button className="btn btn-primary" onClick={() => setShowModal(true)}>
                                Add Sender
                            </button>
                        </div>

                        <div style={{ background: "var(--bg-tertiary)", borderRadius: "var(--radius-md)", padding: 20, marginTop: 24 }}>
                            <div style={{ fontWeight: 600, marginBottom: 12 }}>üí° Quick Setup Tips</div>
                            <div style={{ fontSize: 14, color: "var(--text-secondary)", lineHeight: 1.8 }}>
                                <strong>PrivateEmail:</strong> mail.privateemail.com, Port 465 (SSL)<br />
                                <strong>Gmail:</strong> smtp.gmail.com, Port 465 (SSL) - requires App Password<br />
                                <strong>Outlook:</strong> smtp.office365.com, Port 587 (STARTTLS)<br />
                                <strong>SendGrid:</strong> smtp.sendgrid.net, Port 465 (SSL)
                            </div>
                        </div>
                    </div>
                ) : (
                    <div style={{ display: "grid", gap: 20, gridTemplateColumns: "repeat(auto-fill, minmax(350px, 1fr))" }}>
                        {senders.map((sender: { _id: Id<"senders">; name: string; fromEmail: string; fromName: string; smtpHost: string; smtpPort: number; useSSL: boolean; isDefault: boolean }) => (
                            <div key={sender._id} className="card">
                                <div style={{ display: "flex", justifyContent: "space-between", alignItems: "flex-start", marginBottom: 16 }}>
                                    <div>
                                        <h3 style={{ fontSize: 18, fontWeight: 600, marginBottom: 4 }}>{sender.name}</h3>
                                        <p style={{ fontSize: 14, color: "var(--text-secondary)" }}>{sender.fromEmail}</p>
                                    </div>
                                    {sender.isDefault && <span className="badge badge-info">Default</span>}
                                </div>

                                <div
                                    style={{
                                        background: "var(--bg-tertiary)",
                                        borderRadius: "var(--radius-md)",
                                        padding: 16,
                                        marginBottom: 16,
                                    }}
                                >
                                    <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr", gap: 12, fontSize: 13 }}>
                                        <div>
                                            <span style={{ color: "var(--text-muted)" }}>Host:</span>
                                            <span style={{ marginLeft: 8 }}>{sender.smtpHost}</span>
                                        </div>
                                        <div>
                                            <span style={{ color: "var(--text-muted)" }}>Port:</span>
                                            <span style={{ marginLeft: 8 }}>{sender.smtpPort}</span>
                                        </div>
                                        <div>
                                            <span style={{ color: "var(--text-muted)" }}>SSL:</span>
                                            <span style={{ marginLeft: 8 }}>{sender.useSSL ? "Yes" : "No"}</span>
                                        </div>
                                        <div>
                                            <span style={{ color: "var(--text-muted)" }}>From:</span>
                                            <span style={{ marginLeft: 8 }}>{sender.fromName}</span>
                                        </div>
                                    </div>
                                </div>

                                <div style={{ display: "flex", gap: 8 }}>
                                    <button className="btn btn-secondary btn-sm" style={{ flex: 1 }}>
                                        Edit
                                    </button>
                                    <button
                                        className="btn btn-ghost btn-sm"
                                        onClick={() => handleDelete(sender._id)}
                                        style={{ color: "var(--accent-danger)" }}
                                    >
                                        Delete
                                    </button>
                                </div>
                            </div>
                        ))}
                    </div>
                )}

                {/* Modal */}
                {showModal && (
                    <div className="modal-overlay" onClick={(e) => e.target === e.currentTarget && setShowModal(false)}>
                        <div className="modal" style={{ maxWidth: 560 }}>
                            <div className="modal-header">
                                <h2 className="modal-title">Add Sender</h2>
                                <button className="modal-close" onClick={() => setShowModal(false)}>√ó</button>
                            </div>

                            <div className="modal-body">
                                <div className="form-group">
                                    <label className="form-label">Sender Name *</label>
                                    <input
                                        type="text"
                                        className="form-input"
                                        placeholder="e.g., Marketing"
                                        value={name}
                                        onChange={(e) => setName(e.target.value)}
                                    />
                                </div>

                                <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr", gap: 16 }}>
                                    <div className="form-group">
                                        <label className="form-label">From Email *</label>
                                        <input
                                            type="email"
                                            className="form-input"
                                            placeholder="hello@company.com"
                                            value={fromEmail}
                                            onChange={(e) => setFromEmail(e.target.value)}
                                        />
                                    </div>
                                    <div className="form-group">
                                        <label className="form-label">From Name</label>
                                        <input
                                            type="text"
                                            className="form-input"
                                            placeholder="Company Name"
                                            value={fromName}
                                            onChange={(e) => setFromName(e.target.value)}
                                        />
                                    </div>
                                </div>

                                <div style={{ borderTop: "1px solid var(--border-subtle)", margin: "20px 0", paddingTop: 20 }}>
                                    <div style={{ fontSize: 14, fontWeight: 600, marginBottom: 16 }}>SMTP Configuration</div>
                                </div>

                                <div style={{ display: "grid", gridTemplateColumns: "2fr 1fr", gap: 16 }}>
                                    <div className="form-group">
                                        <label className="form-label">SMTP Host *</label>
                                        <input
                                            type="text"
                                            className="form-input"
                                            placeholder="smtp.example.com"
                                            value={smtpHost}
                                            onChange={(e) => setSmtpHost(e.target.value)}
                                        />
                                    </div>
                                    <div className="form-group">
                                        <label className="form-label">Port *</label>
                                        <input
                                            type="text"
                                            className="form-input"
                                            placeholder="465"
                                            value={smtpPort}
                                            onChange={(e) => setSmtpPort(e.target.value)}
                                        />
                                    </div>
                                </div>

                                <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr", gap: 16 }}>
                                    <div className="form-group">
                                        <label className="form-label">Username</label>
                                        <input
                                            type="text"
                                            className="form-input"
                                            placeholder="Same as email if blank"
                                            value={smtpUser}
                                            onChange={(e) => setSmtpUser(e.target.value)}
                                        />
                                    </div>
                                    <div className="form-group">
                                        <label className="form-label">Password</label>
                                        <input
                                            type="password"
                                            className="form-input"
                                            placeholder="‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢"
                                            value={smtpPass}
                                            onChange={(e) => setSmtpPass(e.target.value)}
                                        />
                                        <p className="form-hint">Set via Convex env vars for security</p>
                                    </div>
                                </div>

                                <div style={{ display: "flex", gap: 24, marginTop: 8 }}>
                                    <label className="form-checkbox">
                                        <input type="checkbox" checked={useSSL} onChange={(e) => setUseSSL(e.target.checked)} />
                                        <span>Use SSL/TLS</span>
                                    </label>
                                    <label className="form-checkbox">
                                        <input type="checkbox" checked={isDefault} onChange={(e) => setIsDefault(e.target.checked)} />
                                        <span>Set as default sender</span>
                                    </label>
                                </div>
                            </div>

                            <div className="modal-footer">
                                <button className="btn btn-secondary" onClick={() => setShowModal(false)}>
                                    Cancel
                                </button>
                                <button
                                    className="btn btn-primary"
                                    onClick={handleCreate}
                                    disabled={!name.trim() || !fromEmail.trim() || !smtpHost.trim() || isSubmitting}
                                >
                                    {isSubmitting ? "Adding..." : "Add Sender"}
                                </button>
                            </div>
                        </div>
                    </div>
                )}
            </div>
        </Navigation>
    );
}
